.panel {
    background-color: #181818;
    color: white;
    width: 95%;
    font-size: 15px;
    margin: 1rem auto;
}

.panel-header {
    padding: 18px;
    border-bottom: 1px solid rgb(53, 53, 53);
}

.panel-title {
    font-size: 1.3em;
    margin-top: 0;
    margin-bottom: .5rem;
}

.accordion {
    background-color: #181818;
    color: white;
    cursor: pointer;
    padding: 10px;
    width: 100%;
    box-sizing: border-box;
    border: none;
    text-align: center;
    outline: none;
    font-size: 15px;
    transition: 0.4s;
}
  
.accordion:hover, .accordion.active {
    background-color: #151515;
}

.drawer {
    margin: 0 .25rem;
    padding: 0 10px;
    background-color: #252525;
    max-height: 0;
    overflow: hidden;
    transition: max-height 0.4s ease-out;
}

.drawer.active {
    max-height: 1000px;
}

.tags {
    list-style-type: none;
    padding: 0;
    display: flex;
    flex-direction: row;
}

.tags li {
    background-color: rgb(42, 167, 209);
    padding: 3px 5px;
    border-radius: 5px;
    margin: 0 2px;
    cursor: default;
}