.slideshow {
    position: relative;
    margin: .5rem 0;
}

.slide {
    /* display: none; */
    margin: 0 auto;
    text-align: center;
    width: 100%;
    max-width: 800px;
}

.slide img {
    max-width: 100%;
    max-height: 400px;
}

.prev,
.next {
    cursor: pointer;
    position: absolute;
    top: 40%;
    width: auto;
    padding: 16px;
    margin-top: -50px;
    color: white;
    font-weight: bold;
    font-size: 20px;
    user-select: none;
}

.prev {
    left: 0;
    border-radius: 0 3px 3px 0;
}

.next {
    right: 0;
    border-radius: 3px 0 0 3px;
}

.prev:hover,
.next:hover {
    background-color: rgba(0, 0, 0, 0.8);
}

.slide-number {
    background-color: rgba(0, 0, 0, 0.4);
    border-radius: 5px;
    color: #f2f2f2;
    font-size: 12px;
    padding: 8px 12px;
    position: absolute;
    top: 0;
}

.caption {
    text-align: center;
    border-top: 1px solid rgb(53, 53, 53);
    border-bottom: 1px solid rgb(53, 53, 53);
    padding: 5px 0;
    margin: 0;
    color: white;
}

.thumbnail-row:after {
    content: "";
    display: table;
    clear: both;
}

.thumbnail-column {
    float: left;
    width: 16.66%;
}

.thumbnail {
    opacity: 0.6;
    cursor: pointer;
    width: 100%;
}

.active,
.thumbnail:hover {
    opacity: 1;
}